"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { MdSearch, MdFilterList, MdClose, MdStar, MdStarOutline, MdVerified, MdAccessTime, MdRateReview, MdAttachMoney, MdTune } from "react-icons/md";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Sheet,
  SheetContent,
  SheetDescription,
  SheetHeader,
  SheetTitle,
  SheetTrigger,
} from "@/components/ui/sheet";
import { Checkbox } from "@/components/ui/checkbox";
import { Label } from "@/components/ui/label";
import { Slider } from "@/components/ui/slider";
import { SellerCard, type SellerData } from "@/components/marketplace";
import { SellerCardGridSkeleton } from "@/components/ui/skeletons";
import { EmptyState } from "@/components/ui/empty-state";

// Use SellerData type from marketplace components
type Member = SellerData;

interface BrowseTabProps {
  experienceId?: string;
  members: Member[];
  isLoading?: boolean;
  standalone?: boolean; // When true, uses /app paths instead of /experiences
}

interface Filters {
  verifiedOnly: boolean;
  hasRateOnly: boolean;
  minRating: number;
  maxPrice: number | null;
  minSessions: number;
  hasReviews: boolean;
}

export function BrowseTab({ experienceId, members, isLoading, standalone }: BrowseTabProps) {
  const [searchQuery, setSearchQuery] = useState("");
  const [sortBy, setSortBy] = useState("recent");
  const [filters, setFilters] = useState<Filters>({
    verifiedOnly: false,
    hasRateOnly: false,
    minRating: 0,
    maxPrice: null,
    minSessions: 0,
    hasReviews: false,
  });
  const [isFilterOpen, setIsFilterOpen] = useState(false);

  // Show all Waliet users toggle (only for /experiences, not standalone)
  const [showAllUsers, setShowAllUsers] = useState(false);
  const [allUsers, setAllUsers] = useState<Member[]>([]);
  const [isLoadingAllUsers, setIsLoadingAllUsers] = useState(false);

  // Fetch all Waliet sellers when toggle is enabled
  useEffect(() => {
    if (showAllUsers && !standalone && allUsers.length === 0) {
      setIsLoadingAllUsers(true);
      fetch("/api/sellers?all=true")
        .then((res) => res.json())
        .then((data) => {
          setAllUsers(data.sellers || []);
        })
        .catch((err) => {
          console.error("Failed to fetch all users:", err);
        })
        .finally(() => {
          setIsLoadingAllUsers(false);
        });
    }
  }, [showAllUsers, standalone, allUsers.length]);

  // Use all users or community members based on toggle
  const displayMembers = showAllUsers && !standalone ? allUsers : members;
  const displayLoading = showAllUsers && !standalone ? isLoadingAllUsers : isLoading;

  // Get max price for slider
  const maxAvailablePrice = Math.max(
    ...displayMembers.filter((m) => m.hourlyRate !== null).map((m) => m.hourlyRate!),
    10000 // Default max $100/hr
  );

  const filteredMembers = displayMembers.filter((member) => {
    const matchesSearch =
      !searchQuery ||
      member.user.name?.toLowerCase().includes(searchQuery.toLowerCase()) ||
      member.bio?.toLowerCase().includes(searchQuery.toLowerCase()) ||
      member.tagline?.toLowerCase().includes(searchQuery.toLowerCase());

    const matchesVerified = !filters.verifiedOnly || member.isVerified;
    const matchesHasRate = !filters.hasRateOnly || member.hourlyRate !== null;
    const matchesMinRating =
      filters.minRating === 0 || (member.averageRating || 0) >= filters.minRating;
    const matchesMaxPrice =
      !filters.maxPrice || (member.hourlyRate && member.hourlyRate <= filters.maxPrice);
    const matchesMinSessions =
      filters.minSessions === 0 || member.totalSessionsCompleted >= filters.minSessions;
    const matchesHasReviews =
      !filters.hasReviews || (member.averageRating !== null && member.averageRating > 0);

    return matchesSearch && matchesVerified && matchesHasRate && matchesMinRating && matchesMaxPrice && matchesMinSessions && matchesHasReviews;
  });

  const sortedMembers = [...filteredMembers].sort((a, b) => {
    switch (sortBy) {
      case "rating":
        return (b.averageRating || 0) - (a.averageRating || 0);
      case "sessions":
        return b.totalSessionsCompleted - a.totalSessionsCompleted;
      case "price_low":
        // Members without rates go to the end
        if (a.hourlyRate === null) return 1;
        if (b.hourlyRate === null) return -1;
        return a.hourlyRate - b.hourlyRate;
      case "price_high":
        if (a.hourlyRate === null) return 1;
        if (b.hourlyRate === null) return -1;
        return b.hourlyRate - a.hourlyRate;
      case "recent":
      default:
        return 0; // Keep original order (by createdAt desc from server)
    }
  });

  const formatRate = (cents: number | null) => {
    if (cents === null) return null;
    return new Intl.NumberFormat("en-US", {
      style: "currency",
      currency: "USD",
      minimumFractionDigits: 0,
    }).format(cents / 100);
  };

  const activeFilterCount = [
    filters.verifiedOnly,
    filters.hasRateOnly,
    filters.minRating > 0,
    filters.maxPrice !== null,
    filters.minSessions > 0,
    filters.hasReviews,
  ].filter(Boolean).length;

  const clearFilters = () => {
    setFilters({
      verifiedOnly: false,
      hasRateOnly: false,
      minSessions: 0,
      hasReviews: false,
      minRating: 0,
      maxPrice: null,
    });
  };

  return (
    <div className="p-4 sm:p-6 md:p-8">
      {/* Header */}
      <div className="mb-6">
        <h1 className="text-2xl font-semibold text-foreground">
          {standalone ? "Browse Sellers" : showAllUsers ? "All Waliet Sellers" : "Community Members"}
        </h1>
        <p className="text-muted-foreground mt-1">
          {standalone
            ? "Find sellers and book their time"
            : showAllUsers
              ? "Browse all Waliet sellers with rates"
              : "Browse members in this community and book their time"}
        </p>
      </div>

      {/* Search & Filters */}
      <div className="flex flex-col sm:flex-row gap-2 mb-6">
        <div className="relative flex-1">
          <MdSearch className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
          <Input
            placeholder="Search by name, skill, or keyword..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="pl-9 h-10"
          />
        </div>

        <div className="flex gap-2">
          {/* Show All Users Toggle (only in /experiences mode) */}
          {!standalone && (
            <Button
              variant="outline"
              onClick={() => setShowAllUsers(!showAllUsers)}
              className="h-10 px-4"
            >
              {showAllUsers ? "All Waliet" : "Community"}
            </Button>
          )}
          <Sheet open={isFilterOpen} onOpenChange={setIsFilterOpen}>
            <SheetTrigger asChild>
              <Button variant="outline" className="h-10 gap-2 px-4">
                <MdFilterList className="h-4 w-4" />
                Filters
                {activeFilterCount > 0 && (
                  <Badge variant="secondary" className="ml-1 h-5 w-5 p-0 flex items-center justify-center text-xs">
                    {activeFilterCount}
                  </Badge>
                )}
              </Button>
            </SheetTrigger>
            <SheetContent className="overflow-y-auto">
              <SheetHeader className="pb-6 border-b border-border">
                <div className="flex items-center justify-between">
                  <div>
                    <SheetTitle className="flex items-center gap-2">
                      <MdTune className="h-5 w-5" />
                      Filters
                    </SheetTitle>
                    <SheetDescription className="mt-1">
                      Find your perfect match
                    </SheetDescription>
                  </div>
                  {activeFilterCount > 0 && (
                    <Button variant="ghost" size="sm" onClick={clearFilters} className="text-muted-foreground hover:text-foreground">
                      Clear all
                    </Button>
                  )}
                </div>
              </SheetHeader>

              <div className="py-6 space-y-8">
                {/* Quick Filters */}
                <div className="space-y-3">
                  <h3 className="text-xs font-medium text-muted-foreground">Quick Filters</h3>
                  <div className="grid grid-cols-2 gap-2">
                    <button
                      onClick={() => setFilters((f) => ({ ...f, verifiedOnly: !f.verifiedOnly }))}
                      className={`flex items-center gap-2 p-3 rounded-xl border transition-all ${
                        filters.verifiedOnly
                          ? "border-primary bg-primary/10 text-primary"
                          : "border-border bg-card hover:border-muted-foreground/30"
                      }`}
                    >
                      <MdVerified className="h-5 w-5" />
                      <span className="text-sm font-medium">Verified</span>
                    </button>
                    <button
                      onClick={() => setFilters((f) => ({ ...f, hasRateOnly: !f.hasRateOnly }))}
                      className={`flex items-center gap-2 p-3 rounded-xl border transition-all ${
                        filters.hasRateOnly
                          ? "border-primary bg-primary/10 text-primary"
                          : "border-border bg-card hover:border-muted-foreground/30"
                      }`}
                    >
                      <MdAttachMoney className="h-5 w-5" />
                      <span className="text-sm font-medium">Has Rate</span>
                    </button>
                    <button
                      onClick={() => setFilters((f) => ({ ...f, hasReviews: !f.hasReviews }))}
                      className={`flex items-center gap-2 p-3 rounded-xl border transition-all ${
                        filters.hasReviews
                          ? "border-primary bg-primary/10 text-primary"
                          : "border-border bg-card hover:border-muted-foreground/30"
                      }`}
                    >
                      <MdRateReview className="h-5 w-5" />
                      <span className="text-sm font-medium">Has Reviews</span>
                    </button>
                    <button
                      onClick={() => setFilters((f) => ({ ...f, minSessions: f.minSessions > 0 ? 0 : 1 }))}
                      className={`flex items-center gap-2 p-3 rounded-xl border transition-all ${
                        filters.minSessions > 0
                          ? "border-primary bg-primary/10 text-primary"
                          : "border-border bg-card hover:border-muted-foreground/30"
                      }`}
                    >
                      <MdAccessTime className="h-5 w-5" />
                      <span className="text-sm font-medium">Experienced</span>
                    </button>
                  </div>
                </div>

                {/* Rating Filter - Clickable Stars */}
                <div className="space-y-4">
                  <div className="flex items-center justify-between">
                    <h3 className="text-xs font-medium text-muted-foreground">Minimum Rating</h3>
                    {filters.minRating > 0 && (
                      <button
                        onClick={() => setFilters((f) => ({ ...f, minRating: 0 }))}
                        className="text-xs text-muted-foreground hover:text-foreground"
                      >
                        Clear
                      </button>
                    )}
                  </div>
                  <div className="flex items-center gap-1">
                    {[1, 2, 3, 4, 5].map((star) => (
                      <button
                        key={star}
                        onClick={() => setFilters((f) => ({ ...f, minRating: f.minRating === star ? 0 : star }))}
                        className="p-1 rounded-lg hover:bg-muted transition-colors"
                      >
                        {star <= filters.minRating ? (
                          <MdStar className="h-8 w-8 text-warning" />
                        ) : (
                          <MdStarOutline className="h-8 w-8 text-muted-foreground/40 hover:text-warning/60" />
                        )}
                      </button>
                    ))}
                    <span className="ml-3 text-sm text-muted-foreground">
                      {filters.minRating > 0 ? `${filters.minRating}+ stars` : "Any rating"}
                    </span>
                  </div>
                </div>

                {/* Experience Level */}
                <div className="space-y-4">
                  <div className="flex items-center justify-between">
                    <h3 className="text-xs font-medium text-muted-foreground">Experience Level</h3>
                    {filters.minSessions > 0 && (
                      <button
                        onClick={() => setFilters((f) => ({ ...f, minSessions: 0 }))}
                        className="text-xs text-muted-foreground hover:text-foreground"
                      >
                        Clear
                      </button>
                    )}
                  </div>
                  <div className="flex flex-wrap gap-2">
                    {[
                      { value: 0, label: "Any" },
                      { value: 1, label: "1+ sessions" },
                      { value: 5, label: "5+ sessions" },
                      { value: 10, label: "10+ sessions" },
                      { value: 25, label: "25+ sessions" },
                    ].map((option) => (
                      <button
                        key={option.value}
                        onClick={() => setFilters((f) => ({ ...f, minSessions: option.value }))}
                        className={`px-3 py-1.5 rounded-full text-sm font-medium transition-all ${
                          filters.minSessions === option.value
                            ? "bg-primary text-primary-foreground"
                            : "bg-muted text-muted-foreground hover:bg-muted/80"
                        }`}
                      >
                        {option.label}
                      </button>
                    ))}
                  </div>
                </div>

                {/* Price Range */}
                <div className="space-y-4">
                  <div className="flex items-center justify-between">
                    <h3 className="text-xs font-medium text-muted-foreground">Max Hourly Rate</h3>
                    {filters.maxPrice !== null && (
                      <button
                        onClick={() => setFilters((f) => ({ ...f, maxPrice: null }))}
                        className="text-xs text-muted-foreground hover:text-foreground"
                      >
                        Clear
                      </button>
                    )}
                  </div>
                  <div className="space-y-4">
                    <div className="flex flex-wrap gap-2">
                      {[
                        { value: null, label: "Any" },
                        { value: 2500, label: "$25" },
                        { value: 5000, label: "$50" },
                        { value: 10000, label: "$100" },
                        { value: 20000, label: "$200" },
                      ].map((option) => (
                        <button
                          key={option.value ?? "any"}
                          onClick={() => setFilters((f) => ({ ...f, maxPrice: option.value }))}
                          className={`px-3 py-1.5 rounded-full text-sm font-medium transition-all ${
                            filters.maxPrice === option.value
                              ? "bg-primary text-primary-foreground"
                              : "bg-muted text-muted-foreground hover:bg-muted/80"
                          }`}
                        >
                          {option.label}
                        </button>
                      ))}
                    </div>
                    <div className="pt-2">
                      <Slider
                        value={[filters.maxPrice || maxAvailablePrice]}
                        onValueChange={([value]) =>
                          setFilters((f) => ({ ...f, maxPrice: value === maxAvailablePrice ? null : value }))
                        }
                        max={maxAvailablePrice}
                        min={500}
                        step={500}
                        className="w-full"
                      />
                      <div className="flex justify-between text-xs text-muted-foreground mt-2">
                        <span>$5/hr</span>
                        <span className="font-medium text-foreground">
                          {filters.maxPrice ? formatRate(filters.maxPrice) + "/hr" : "No limit"}
                        </span>
                        <span>{formatRate(maxAvailablePrice)}/hr</span>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Active Filters Summary */}
                {activeFilterCount > 0 && (
                  <div className="pt-4 border-t border-border">
                    <div className="flex items-center justify-between text-sm">
                      <span className="text-muted-foreground">
                        {activeFilterCount} filter{activeFilterCount !== 1 ? "s" : ""} active
                      </span>
                      <span className="font-medium text-foreground">
                        {sortedMembers.length} result{sortedMembers.length !== 1 ? "s" : ""}
                      </span>
                    </div>
                  </div>
                )}
              </div>
            </SheetContent>
          </Sheet>

          <Select value={sortBy} onValueChange={setSortBy}>
            <SelectTrigger className="h-10 w-[160px]">
              <SelectValue placeholder="Sort by" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="recent">Recently Joined</SelectItem>
              <SelectItem value="rating">Top Rated</SelectItem>
              <SelectItem value="sessions">Most Sessions</SelectItem>
              <SelectItem value="price_low">Price: Low to High</SelectItem>
              <SelectItem value="price_high">Price: High to Low</SelectItem>
            </SelectContent>
          </Select>
        </div>
      </div>

      {/* Active Filters Display */}
      {activeFilterCount > 0 && (
        <div className="flex flex-wrap gap-2 mb-4">
          {filters.verifiedOnly && (
            <Badge variant="secondary" className="gap-1 pl-2">
              <MdVerified className="h-3 w-3" />
              Verified
              <button onClick={() => setFilters((f) => ({ ...f, verifiedOnly: false }))} className="ml-1 hover:text-foreground">
                <MdClose className="h-3 w-3" />
              </button>
            </Badge>
          )}
          {filters.hasRateOnly && (
            <Badge variant="secondary" className="gap-1 pl-2">
              <MdAttachMoney className="h-3 w-3" />
              Has Rate
              <button onClick={() => setFilters((f) => ({ ...f, hasRateOnly: false }))} className="ml-1 hover:text-foreground">
                <MdClose className="h-3 w-3" />
              </button>
            </Badge>
          )}
          {filters.hasReviews && (
            <Badge variant="secondary" className="gap-1 pl-2">
              <MdRateReview className="h-3 w-3" />
              Has Reviews
              <button onClick={() => setFilters((f) => ({ ...f, hasReviews: false }))} className="ml-1 hover:text-foreground">
                <MdClose className="h-3 w-3" />
              </button>
            </Badge>
          )}
          {filters.minRating > 0 && (
            <Badge variant="secondary" className="gap-1 pl-2">
              <MdStar className="h-3 w-3 text-warning" />
              {filters.minRating}+ Stars
              <button onClick={() => setFilters((f) => ({ ...f, minRating: 0 }))} className="ml-1 hover:text-foreground">
                <MdClose className="h-3 w-3" />
              </button>
            </Badge>
          )}
          {filters.minSessions > 0 && (
            <Badge variant="secondary" className="gap-1 pl-2">
              <MdAccessTime className="h-3 w-3" />
              {filters.minSessions}+ Sessions
              <button onClick={() => setFilters((f) => ({ ...f, minSessions: 0 }))} className="ml-1 hover:text-foreground">
                <MdClose className="h-3 w-3" />
              </button>
            </Badge>
          )}
          {filters.maxPrice && (
            <Badge variant="secondary" className="gap-1 pl-2">
              Max {formatRate(filters.maxPrice)}/hr
              <button onClick={() => setFilters((f) => ({ ...f, maxPrice: null }))} className="ml-1 hover:text-foreground">
                <MdClose className="h-3 w-3" />
              </button>
            </Badge>
          )}
        </div>
      )}

      {/* Results Count */}
      <p className="text-sm text-muted-foreground mb-4">
        {sortedMembers.length} {sortedMembers.length === 1 ? "seller" : "sellers"}
        {!standalone && !showAllUsers && " in this community"}
      </p>

      {/* Members Grid */}
      {displayLoading ? (
        <SellerCardGridSkeleton count={6} />
      ) : sortedMembers.length === 0 ? (
        <Card variant="bordered">
          <CardContent className="p-0">
            {searchQuery || activeFilterCount > 0 ? (
              <EmptyState
                preset="no-search-results"
                action={{
                  label: "Clear Filters",
                  onClick: () => {
                    setSearchQuery("");
                    clearFilters();
                  },
                }}
              />
            ) : (
              <EmptyState
                preset="no-members"
              />
            )}
          </CardContent>
        </Card>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {sortedMembers.map((member) => (
            <SellerCard
              key={member.userId}
              seller={member}
              experienceId={experienceId}
              standalone={standalone}
            />
          ))}
        </div>
      )}
    </div>
  );
}
